body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.my-panel{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 10%;
  }
  
  .my-panel h1{
    margin: 0;
    padding: 0;
    font-size: 20px;
    padding: 10px 2px;
    
  }
  .my-panel p{
    padding: 0;
    margin: 0 0 15% 0;
    font-size: 16px;
  }
  .img-rounded{
    border-radius: 50%;
    width: 65%;
    max-width: 170px;
  }
  
.works{
    display: flex;
    flex-direction: column;
}

.works > div:nth-child(odd){
    background-color: #ccc;
}

.works > div{
    padding: 5%;
}
html, body, #root{
  min-height: 100vh !important;
  height: 100vh;
  overflow: hidden;
}


.container{
  display: flex;
  flex-direction: row;
  font-family: roboto;
  height: 100vh;
  
}

.left{
  background-color: teal;
  min-height: 100%;
  flex: 0 0 25vw;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
} 


.right{
  background-color: #eee; /*orangered;*/
  min-height: 100vh;
  flex: 1 1 auto;
  overflow: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}



@media all and (max-width: 600px){
  .container{
      flex-direction: column;
  }
  .left{
      width:100%;
      max-height: 36%;
      position: relative;
  }
}



